body {
  color: black;
}
body {
  background-color: #EFEFF2;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 26px;
  color: #4c4c4c; }

img, picture {
  width: 100%;
  height: auto; }

#primary .card nav a.active {
  background-color: #FFFFFF !important;
  color: #0E374E !important; }

a.primary-button, a.secondary-button, a.outline-button {
  text-decoration: none; }

.primary-button {
  border-radius: 100px;
  padding: 6px 20px 6px 20px;
  height: 40px;
  text-align: center;
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.5px; }

select.secondary-button {
  -webkit-appearance: none;
  padding: 6px 20px 6px 20px;
  border-radius: 100px;
  cursor: pointer; }

.secondary-button {
  border-radius: 100px;
  padding: 6px 20px 6px 20px;
  height: 24px;
  line-height: 0;
  text-align: center;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.5px;
  border: 2px solid #FFFFFF; }

.outline-button {
  border-radius: 100px;
  padding: 6px 20px 6px 20px;
  height: 46px;
  width: 100%;
  text-align: center;
  color: #0E374E;
  font-size: 12px;
  font-weight: 600;
  letter-spacing: 0.5px;
  background: transparent;
  line-height: 2.5;
  border: 2px solid #22768F; }

.button-dark {
  background: #FFFFFF;
  margin-left: 10px; }

.button-dark:hover {
  background: #174B68; }

.button-light {
  background: #DFAD63;
  color: #0E374E; }

.button-light:hover {
  background: #F2C27A; }

a:visited {
  color: inherit; }

.card-toggle-button {
  border: none;
  background: none;
  width: 30px;
  height: 30px;
  font-size: 30px;
  font-weight: 500;
  padding-top: 10px;
  margin-top: -45px;
  color: #22768F; }

